*{
    margin: 0;
    padding: 0;
    outline: 0; 
    box-sizing: border-box;
}

body{
   font: 400 90% Roboto, sans-serif;
   background: #f0f0f5;
   -webkit-font-smoothing: antialiased
}

input, button, textarea{
    font: 400 18px Roboto, sans-serif;
}

button{
    cursor: pointer;
}
/* floats */
.float-l { float: left; }

.float-l img{
   padding-right: 50px;
}

.float-r{
   padding-right: 50px;
}

.fa-search{
   color: white;
}

.fixed-header {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%; 
   background-color: white;
   z-index: 12;
}

.links-header-black{
   color: black;
}

@media screen and (max-width: 650px) {
   header {
      display: flex;
      position: absolute;
      /* position: fixed; */
      min-height: 90px;
      top: 0;
      right: 0;
      left: 0;
      background-color: white;
      z-index: 2;
      padding: 10px 0px 0px 75px;
      z-index: 10;

   }

   header .control-nav {
      position: absolute;
      left: 20px;
      top: 20px;
      display: block;
      width: 30px;
      padding: 5px 0;
      border: solid black;
      border-width: 3px 0;
      z-index: 2;
      cursor: pointer;
   }
    
   .float-l img{
      position: absolute;
      top: 20px;
   }
 
   header .control-nav:before {
      content: "";
      display: block;
      height: 3px;
      background: black;
   }
  
   header .control-nav-close {
      position: fixed;
      right: 0;
      top: 0;
      bottom: 0;
      left: 0;
      display: block;
      z-index: 1;
      background: rgba(0,0,0,0.4);
      transition: all 500ms ease;
      transform: translate(100%, 0);
   }
  
   header nav, .nav-secondary {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      width: 250px;
      border-left: 1px solid #ccc;
      background: #fff;
      overflow-x: auto;
      z-index: 2;
      transition: all 500ms ease;
      transform: translate(100%, 0);
   }
}

@media screen and (min-width: 650px) {
   header {
      display: flex;
      min-height: 60px;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      background: transparent;
      z-index: 2;
      padding: 10px 0px 0px 100px;
      z-index: 10;

   }
}

header ul {
  padding: 15px 10px 0 0;
}

header li:first-child {
  border: none;
}

header li a {
   font: normal 600 14px / 18px "Open Sans", Helvetica, Arial, Verdana, sans-serif;
   display: block;
   padding: 0 10px;
   color: white;
   line-height: 30px;
   text-decoration: none;
   transition: all 300ms ease;
}

input#control-nav {  
  visibility: hidden;
  position: absolute;
  left: -9999px;
  opacity: 0;
}

#control-nav:checked ~ .control-nav-close {
   transform: translate(0, 0);
}
 
#control-nav:checked ~ nav {
   transform: translate(0, 0);
}

@media screen and (min-width: 650px) and (max-width: 950px) {
   header .control-nav {
     position: absolute;
     left: 20px;
     top: 20px;
     display: block;
     width: 30px;
     padding: 5px 0;
     border: solid white;
     border-width: 3px 0;
     z-index: 2;
     cursor: pointer;
   }
   
   .float-l img{
      position: absolute;
      top: 20px;
      right: 20px;
   }

   header .control-nav:before {
     content: "";
     display: block;
     height: 3px;
     background: white;
   }
 
   header .control-nav-close {
     position: fixed;
     right: 0;
     top: 0;
     bottom: 0;
     left: 0;
     display: block;
     z-index: 1;
     background: rgba(0,0,0,0.4);
     transition: all 500ms ease;
     transform: translate(100%, 0);
   }
 
   header nav {
     position: fixed;
     top: 0;
     right: 0;
     bottom: 0;
     width: 250px;
     border-left: 1px solid #ccc;
     background: #fff;
     overflow-x: auto;
     z-index: 2;
     transition: all 500ms ease;
     transform: translate(100%, 0);
   }
}

