/* Overlay */
#overlay {
   position: fixed;
   /* display: none; */
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background-color: rgba(0,0,0,0.8);
   z-index: 12;
   cursor: pointer;
   overflow: hidden;
}

.overlay-off{
   display: none;
}

.button-overlay{
   position: absolute;
   right: 80px;
   top: 5%;
   height: 30px;
   width: 30px;
   background-color: transparent;
   color: white;
   border-radius: 50%;
}

.overlay-information{
   position: absolute;
   width: 100%;
   top: 15%;
}

.overlay-content-title{
   border-bottom: 2px solid #ff9d1c;
   max-width: 80%;
   margin: auto;
}

.overlay-content-title h2{
   text-align: center;
   color: white;
}

.overlay-content-description{
   /* border-bottom: 2px solid #ff9d1c; */
   max-width: 70%;
   margin: auto;
}

.overlay-content-description p {
   text-align: center;
   color: white;
}

.box-button{
   display: flex;
   align-items: center;
   justify-content: center;
   padding-top: 40px;
}

.button-github{
   width: 20%;
   height: 40px;
   background-color: transparent;
   border-color: white;
   border-radius: 30px;
}

.button-github a{
   color: white;
   text-decoration: none;
   font-weight: bold;
}

/* Slide */

@media screen and (min-width: 600px) {
   .slide-container {
      width: 100%;
   }

   .each-slide {
      display: flex;
      background-size: cover;
      height: 100%;
   }

   .slide-container img{
      position: relative;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      background-size: cover;
      background-position: center;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      
   }

   .text-float-left{
      display: grid;
      grid-template-columns: 600px;
      position: absolute;
      bottom: 30%;
   }

   .text-float-right{
      display: grid;
      grid-template-columns: 600px;
      position: absolute;
      bottom: 30%;
      margin-left: 700px;
   }
}

@media screen and (max-width: 600px) {
   .slide-container {
      width: 100%;
   }

   .each-slide {
      display: flex;
      justify-content: center;
      background-size: cover;
      height: 300px;
   }

   .slide-container img{
      position: relative;
       overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      padding-top: 91px;
   } 

   .text-float-right{
      display: grid;
      grid-template-columns: 250px;
      height: 100px;
      position: absolute;
      bottom: 30%;
   }

   .text-float-left{
      display: grid;
      grid-template-columns: 250px;
      height: 100px;
      position: absolute;
      bottom: 30%;
   }

   .each-slide > h1 {
      font-family: "Open Sans", Helvetica, Arial, Verdana, sans-serif;
      font-size: calc(2vh + 1vw);
      font-weight: 600;
      color: white;
      padding: 30px;
      text-align: left;
   }
}
  
h3 {
   text-align: center; 
}

h2{
   font: normal 600 34px / 43px "Open Sans", Helvetica, Arial, Verdana, sans-serif;
}

@media screen and (min-width: 600px) {
   .each-slide > h1 {
      font-family: "Open Sans", Helvetica, Arial, Verdana, sans-serif;
      font-size: calc(2em + 1vw);
      font-weight: 600;
      color: white;
      padding: 30px;
      text-align: left;
   }
}

