   padding: 90px 0px 90px 0px;
}

.what-we-do button{
   height: 49px;
   width: 123px;
   background-color:#ff9400;
   border: none;
   color: white;
}

/* Titles */


.title{
   text-align: center;
   text-transform: none;
   clear: both;
   color: #333539;
   margin-bottom: 30px;
}

@media (max-width: 425px){
   .title-market{
      min-width: 90%;
   }
}

.title-market{
   width: 40%;
   align-items: center;
   justify-content: center;
   padding-bottom: 30px;

   text-align: center;

   margin: auto;
   display: grid;
   grid-template-rows: repeat(auto-fit, minmax(50px, 1fr));
}

.title-market h2{
   color: white;
}

.sub-title{
   text-align: center;
   font: normal 600 22px / 26px "Open Sans", Helvetica, Arial, Verdana, sans-serif;
   text-transform: none;
   clear: both;
   color: #333539;
   /* margin-top: 40px;
   margin-bottom: 30px; */
}



/* text */

p {
   font: normal 15px / 25px "Open Sans", Helvetica, Arial, Verdana, sans-serif;
   color: #333539;
   text-align: center;
   display: block;
   -webkit-margin-before: 1em;
           margin-block-start: 1em;
   -webkit-margin-after: 1em;
           margin-block-end: 1em;
   -webkit-margin-start: 0px;
           margin-inline-start: 0px;
   -webkit-margin-end: 0px;
           margin-inline-end: 0px;
}

/* Card Column  */
.row {
   width: 90%;
   max-width: 90%;
   margin: auto;

   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
   grid-gap: 20px;
}

.column {
   display: flex;
   flex-direction: column;
   cursor: pointer;
   transition: all 0.3s ease 0s;
   padding-bottom: 40px;
}

.container-what-we-do .column{
   float: left;
   width: 20%;
   padding: 0 5px;
}
 
@media screen and (max-width: 600px) {
   .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
   }
   .container-what-we-do .column{
      width: 100%;
      display: block;
      margin-bottom: 20px;
   }
   .container-what-we-do{
      padding: 40px 0px 40px 0px;
   }
}

